.card-container {
    position: relative;
    z-index: 0;
}

.statastics-container {
    padding: 1rem 1.5rem;
    /* background: #f2f2f2cb; */
}

.statastics-title {
    text-align: center;
    margin-bottom: 1.5rem;
    color: hsl(255, 11%, 22%);
    font-size: 28px;
}

.description {
    text-align: center;
    margin-bottom: 4rem;
    color: hsl(257, 7%, 63%);
    font-size: 16px;
    padding: 0 0.75rem;
    line-height: 1.7;
}

.ribbon {
    position: absolute;
    top: 0;
    left: 49%;
    height: 100%;
    width: 8px;
    background-color: hsl(180, 66%, 49%);
    z-index: -1;
}

@media(min-width: 900px) and (max-width: 1440px) {
    .statastics-container {
        margin-top: -80px;
        padding: 4rem 10% 4rem 10%;
    }
    .statastics-title {
        font-size: 32px;
        margin-bottom: 1rem;
    }
    .description {
        padding: 0 2rem;
    }
    .card-container {
        height: 20rem;
        display: grid;
        grid-column-gap: 1.5rem;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 40px 40px 1fr 40px 40px;
    }
    .ribbon {
        top: 50%;
        height: 8px;
        width: 100%;
        top: 50%;
        left: 0;
        z-index: 0;
    }
}


