.result-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /* padding: 1rem; */
    background: white;
    border-radius: 5px;
    font-size: 18px;
}

.provided-link {
    padding: 1rem;
    border-bottom: 0.5px solid #ddd;
    word-break: break-word;
}

.generated-short-link {
    color: hsl(180, 66%, 49%);
}

.long-short-btn {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: stretch;
        align-items: stretch;
    gap: 1rem;
    padding: 1rem;
}

.copy-btn {
    border-radius: 4px !important;
}

@media (min-width: 900px) and (max-width: 1440px) {
    .result-container {
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: justify;
            justify-content: space-between;
        -ms-flex-align: center;
            align-items: center;
        font-size: 14px;
    }
    .provided-link {
        border: none;
    }
    .long-short-btn {
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-align: center;
            align-items: center;
    }
    .copy-btn {
        padding: 0.2rem 1rem !important;
        font-size: 14px !important;
    }
}


