.footer {
    background-color: hsl(260, 8%, 14%);
    text-align: center;
    padding: 2.25rem;
}

.footer-logo {
    margin-bottom: 2.25rem;
    line-height: 1;
}

@media(min-width: 900px) and (max-width: 1440px) {
    .footer {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
        -ms-flex-align: start;
            align-items: flex-start;
        padding-left: 10%;
        padding-right: 10%;
    }
}


